.plan {
	padding: 20px 15px 20px;
	&__container {
		max-width: 1374px;
		padding: 0 30px; }
	&__title {
		margin-bottom: 25px; }
	&__block {
		display: flex;
		overflow: hidden;
		padding: 45px 30px 15px;
		background: $white;
		border-radius: 10px;
		border: 1px solid #d3c17f;

		&-view {
			position: relative;
			flex: 1 1 auto;
			padding-right: 85px; }
		&-data {
			flex: 0 0 380px;
			padding: 23px 0 0; }

		&_has-compass::before {
			content: '';
			position: absolute;
			top: 25px;
			left: 10px;
			@include s('compass'); } }

	&__street {
		pointer-events: none;
		position: absolute;
		color: $crimson;
		font-size: 30px;
		@include os-r;
		text-align: center;
		span {
			position: relative;
			display: inline-block;
			vertical-align: middle; }
		&_1 {
			top: -40px;
			left: 0;
			right: 0; }
		&_2 {
			top: -100px;
			left: 0;
			right: 0;
			bottom: 0;
			@include rotate(-90); }
		&-text {
			&_has-label::before {
				margin-right: 70px;
				content: '';
				display: inline-block;
				vertical-align: middle;
				@include rotate(90);
				@include s('plan-str-label'); } } }

	&__look {
		display: none;
		&.slick-initialized {
			display: block; }
		&-btn {
			position: absolute;
			right: -64.7%;
			font-size: 0;
			&_prev {
				top: -30px;
				@include rotate(-90); }
			&_next {
				top: 70px;
				@include rotate(90); }
			&-icon {
				fill: $crimson;
				font-size: 24px; } }
		&-btn.slick-disabled &-btn-icon {
			fill: rgba($crimson, .3); } }

	&__status {
		position: absolute;
		top: -1%;
		right: -42%;
		color: $crimson;
		text-align: center;
		line-height: 1;
		&-count {
			@include os-b;
			font-size: 50px; }
		&-text {
			margin-top: -5px;
			@include os-r;
			font-size: 24px; } }

	&_flat &__block {
		position: relative;
		padding: 50px 30px 20px 30px; }
	&_flat &__block-data {
		padding: 80px 0 0; }
	&_flat &__block-view {
		flex: 1 1 auto;
		min-width: 1px;
		padding: 0 85px 0 110px; } }

/* plan-view
	----------------------------------- */
.plan-view {
	&__top {
		display: flex;
		align-items: center;
		margin-bottom: 30px; }
	&__options {
		display: flex;
		margin-right: 60px;
		&-btn {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 102px;
			height: 102px;
			border-radius: 50%;
			overflow: hidden;
			color: $crimson;
			text-align: center;
			@include os-r;
			font-size: 17px;
			line-height: 22px;
			background: url("../img/bg/cat-img_3.jpg") no-repeat 50% 50%/cover;
			transition: color .5s ease;
			&::after {
				content: '';
				@include coverdiv;
				z-index: 1;
				background: rgba($beige, .9);
				transition: background .5s ease; }
			&_bold {
				@include os-b;
				font-size: 20px;
				text-transform: uppercase; }
			&:not(:first-child) {
				margin-left: 20px; }
			&.is-active {
				pointer-events: none;
				color: $white;
				&::after {
					background: rgba($crimson, .9); } }
			span {
				z-index: 2; } } }

	&__actions {
		display: flex;
		flex-direction: column;
		&-link {
			position: relative;
			font-size: 18px;
			color: $grey;
			&::before {
				content: '';
				display: inline-block;
				vertical-align: middle; }
			&_download::before {
				@include s('download'); }
			&_print::before {
				@include s('printer'); }
			&_star::before {
				@include s('star'); }
			&:not(:first-child) {
				margin-top: 6px; }
			.no-touch &:hover span {
				color: $crimson;
				text-decoration: underline; }
			span {
				display: inline-block;
				vertical-align: middle;
				margin-left: 15px; } } }

	&__picture {
		min-height: 475px; } }

/* request-form
	----------------------------------- */
.request-form {
	padding: 25px 28px 40px;
	border-radius: 10px;
	background-color: #e7d3a9;
	&__title {
		margin-bottom: 20px;
		@include f('con', 23);
		color: $crimson;
		text-align: center; }
	&__row {
		display: flex; }
	&__field {
		flex: 1 1 252px;
		margin-right: 9px; }
	&__input {
		border: 1px solid #dcdcdc; }
	&__btn {
		padding: 0 22px;
		height: 42px;
		line-height: 42px; }

	&_plan {
		margin-top: 30px;
		margin-left: -12px; } }

/* plan-data
	----------------------------------- */
.plan-data {
	&__title {
		margin-bottom: 80px;
		padding-right: 80px;
		@include os-b;
		font-size: 39px;
		text-transform: uppercase;
		color: $crimson; }
	&__sub-title {
		display: inline-block;
		margin-bottom: 45px;
		position: relative;
		padding-right: 50px;
		color: $grey;
		@include os-b;
		font-size: 24px;
		&::after {
			content: '';
			position: absolute;
			top: calc(50% - 13px);
			right: 0;
			@include s('double-arrs'); } }

	&__text {
		margin-bottom: 40px; }
	&__list {
		&-item {
			&:not(:first-child) {
				margin-top: 17px; } }
		&-text {
			@include os-r; }
		&-definition {
			@include os-b;
			color: $crimson; }
		span {
			display: inline-block;
			vertical-align: middle;
			line-height: 28px;
			font-size: 24px; } } }

.gen-plan {
	position: relative;
	width: 260px;
	&__title {
		margin-bottom: 10px;
		text-transform: uppercase;
		font-size: 24px;
		color: $crimson;
		@include os-b; }
	&__svg {
		position: absolute;
		top: 34px;
		right: 0;
		rect {
			pointer-events: none; }
		path, rect {
			opacity: 0;
			cursor: pointer;
			@include hover {
				fill: $crimson;
				opacity: .3; } }
		[data-active-section='is-active'] {
			fill: $crimson;
			opacity: .65;
			pointer-events: none; } } }
