.plan-view__actions-link span { display: inline-block; vertical-align: middle; margin-left: 15px; }

.plan-view__picture { min-height: 475px; }

/* request-form ----------------------------------- */
.request-form { padding: 25px 28px 40px; border-radius: 10px; background-color: #e7d3a9; }

