/* plan-data ----------------------------------- */
.plan-data__title { margin-bottom: 80px; padding-right: 80px; font-family: 'Open Sans', Arial, sans-serif; font-weight: 700; font-size: 39px; text-transform: uppercase; color: #7b232c; }

.plan-data__sub-title { display: inline-block; margin-bottom: 45px; position: relative; padding-right: 50px; color: #444444; font-family: 'Open Sans', Arial, sans-serif; font-weight: 700; font-size: 24px; }

