@keyframes dotBounce { from, 20%, 40%, 60%, 80%, to { animation-timing-function: ease; }
  0% { transform: scale3d(1, 1, 1); }
  20% { transform: scale3d(1, 1, 1); }
  40% { transform: scale3d(0.9, 0.9, 0.9); }
  60% { transform: scale3d(1, 1, 1);
    background: #f0d4aa;
    border-color: #7b232c; }
  80% { transform: scale3d(0.8, 0.8, 0.8); }
  to { transform: scale3d(1, 1, 1); } }

