.container {
	margin: 0 auto;
	padding: 0 15px;
	&_has-decor {
		position: relative;
		&::after, &::before {
			content: '';
			position: absolute;
			top: calc(50% - 155px);
			@include s('vert-decor_sm'); }
		&::before {
			left: 4px; }
		&::after {
			right: 4px;
			@include rotate(180); } } }
.main {
	position: relative;
	min-height: 100vh;
	margin-left: 400px;
	overflow-y: auto;
	overflow-x: hidden;
	transition: transform .25s linear;
	@include rmin(767) {
		@include scrollbar {
			width: 8px;
			box-shadow: inset 0 0 6px rgba($crimson, .5);
			background-color: $beige; }
		@include scrollbar-thumb {
			background-color: $crimson; } }
	&.is-active {
		transform: translateX(330px);
		@include xs {
			transform: translateX(0); } }
	@include lg {
		margin-left: 330px; }
	@include r(1279) {
		margin-left: 0; }

	&_home {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 400px;
		margin-left: 0; }

	&_has-decor {
		background: url("../img/bg/bg-pattern.png"); } }
