.header {
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	z-index: 100;
	width: 400px;
	background: #f0d4aa url("../img/bg/header-decor_bg.png") no-repeat 50% 50%;
	border-right: 3px solid $crimson;
	min-height: 100vh;
	overflow-y: auto;
	@include rmin(767) {
		@include scrollbar {
			width: 4px;
			box-shadow: inset 0 0 6px rgba($crimson, .5);
			background-color: $beige; }
		@include scrollbar-thumb {
			background-color: $crimson; } }
	@include lg {
		width: 330px; }
	@include r(1279) {
		transform: translateX(-100%);
		transition: transform .25s linear; }
	@include xs {
		width: 100%;
		background-repeat: repeat-x;
		border: 0;
		overflow: auto; }
	&.is-visible {
		transform: translateX(0); }

	&__in {
		display: flex;
		flex-direction: column;
		max-width: 300px;
		margin: 0 auto;
		padding: 20px 0 25px;
		min-height: 100vh;
		@include lg {
			padding: 15px 0; }
		&::after {
			pointer-events: none;
			position: absolute;
			top: calc(45% - 163px);
			right: 0;
			z-index: 1;
			content: '';
			@include s('vert-decor_red');
			width: 26px;
			@include xs {
				display: none; } } }
	&__title-decor {
		margin: auto 0 63px;
		@include lg {
			margin: auto 0 35px; } }

	&__logo {
		display: block;
		margin: auto auto 40px;
		@include lg {
			width: 230px;
			margin: 0 auto 20px; }
		img {
			display: block;
			margin: 0 auto;
			@include lg {
			    @include gpu; } } }
	&__menu {
		padding: 0 0 35px 55px; }

	h1 {
		@include hide-text; } }

/* menu
	----------------------------------- */
.menu {
	&__list {
		$list: &;
		@include os-r;
		line-height: 1;
		&-item {
			-webkit-tap-highlight-color: transparent;
			&:not(:first-child) {
				margin-top: 6px; }
			&.is-active span {
				border-bottom-color: $crimson;
				color: $crimson; }
			&.is-active #{$list}-icon {
				transform: scaleY(-1); } }
		&-link {
			font-size: 18px;
			cursor: pointer;
			@include hover {
				color: $crimson; }
			@include lg {
				font-size: 16px; }
			span {
				display: inline-block;
				vertical-align: middle;
				padding-bottom: 3px;
				border-bottom: 1px solid transparent; } }
		&-icon {
			display: inline-block;
			vertical-align: baseline;
			margin-left: 6px;
			fill: $crimson;
			font-size: 6px;
			@include tr(transform .25s ease-in); } }
	&__sub-list {
		display: none;
		padding: 25px 0 25px 6px;
		@include lg {
			padding: 15px 0 15px 6px; }
		li {
			position: relative;
			padding-left: 30px;
			-webkit-tap-highlight-color: transparent;
			&::before {
				content: '';
				position: absolute;
				left: 0;
				top: 4px;
				z-index: 1;
				@include s('arr-long'); }
			&:not(:first-child) {
				margin-top: 8px; }
			a {
				font-size: 16px;
				@include hover {
					color: $crimson; } } } } }

/* address
	----------------------------------- */
.address {
	$this: &;
	@include os-r;
	&__item {
		position: relative;
		margin-bottom: 10px;
		padding-left: 40px;
		@include lg {
			padding-left: 30px;
			margin-bottom: 10px; }
		&::before {
			content: '';
			position: absolute;
			top: 8px;
			left: 0;
			@include lg {
				top: 3px; } }
		&_link {
			font-size: 20px;
			line-height: 44px;
			@include lg {
				font-size: 16px;
				line-height: 30px; }
			&::before {
				@include s('loc-marker'); }
			span {
				border-bottom: 2px dashed #262423;
				transition: .2s;
				@include hover {
					border-bottom-color: transparent; } } }
		&_tel {
			font-size: 24px;
			line-height: 39px;
			@include lg {
				font-size: 18px;
				line-height: 30px; }
			&::before {
				@include s('phone'); } } }

	&_header {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		padding: 0 18px;
		@include lg {
			align-items: center;
			margin-top: auto; }
		#{$this}__btn {
			align-self: center;
			@include lg {
				font-size: 15px; } } } }

