.parking {
	padding: 30px 15px 0;
	&__title {
		margin-bottom: 20px; }
	&__container {
		max-width: 1300px; }
	&__block {
		position: relative;
		height: 845px;
		@include clr; }
	&__data {
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0; }
	&__floor {
		width: 134px;
		height: 142px;
		padding: 33px 15px;
		border-radius: 10px;
		background-color: #e7d3a9;
		text-align: center;
		line-height: 1;
		animation: fadeIn 1.5s ease-in-out;
		@include nosel;
		cursor: default;
		&-count {
			font-size: 50px;
			color: $crimson;
			@include os-b; }
		&-text {
			margin-top: -5px;
			color: $crimson;
			font-size: 24px;
			@include os-r; } }
	&__info {
		display: none;
		min-width: 362px;
		margin-top: 40px;
		padding: 30px 5px 25px 25px;
		border-radius: 10px;
		background-color: #e7d3a9;
		font-size: 24px;
		@include nosel;
		&.is-visible {
			display: block;
			animation: flipInX 1s ease-in; }
		&-item {
			line-height: 1;
			&:not(:first-child) {
				margin-top: 10px; } }
		&-text {
			color: $grey;
			@include os-r; }
		&-data {
			color: $crimson;
			@include os-b; }
		span {
			display: inline-block;
			vertical-align: middle; }
		sup {
			font-size: 12px;
			vertical-align: super; } }

	&__scheme-wrap {
		float: right;
		width: percentage(1106 / 1270); }
	&__scheme {
		display: none;
		&.slick-initialized {
			display: block; }
		&-item {
			position: relative;
			&.slick-active {
				animation: fadeIn 1.5s ease-in-out; } }
		&-btn {
			position: absolute;
			left: -9.8%;
			z-index: 3;
			font-size: 0;
			animation: fadeIn 1.5s ease-in-out;
			&_prev {
				top: 1%;
				@include rotate(-90); }
			&_next {
				top: 12.5%;
				@include rotate(90); }
			&-icon {
				fill: $crimson;
				font-size: 24px; } }
		&-btn.slick-disabled &-btn-icon {
			fill: rgba(123,35,44,.3); } }

	&__place {
		position: absolute;
		z-index: 2;
		cursor: pointer;
		@include tr(background .3s);
		@include hover {
			background: rgba($crimson, .3); }
