.history {
	padding: 60px 20px 15px 40px;
	@include lg {
		padding: 60px 20px 15px 30px; }
	@include xs {
		padding: 70px 20px 30px; }
	&__container {
		padding: 0; }
	&__title span {
		padding: 0 96px; }
	&__article {
		margin: 60px 0 0;
		color: $grey;
		@include xs {
			margin: 40px 0 0; }
		&-top {
			margin-bottom: 30px;
			@include clr;
			@include lg {
				margin-bottom: 15px; } }
		&-title {
			margin-bottom: 25px;
			padding-right: 190px;
			@include f('con', 24);
			@include lg {
				padding-right: 150px;
				margin-bottom: 10px;
				font-size: 22px; }
			@include sm {
				padding-right: 0; } }
		&-img {
			position: relative;
			float: left;
			width: percentage(465 / 1452);
			margin-right: 2.8745%;
			@include lg {
				margin-bottom: 20px; }
			@include xs {
				float: none;
				width: 100%; }
			&::before, &::after {
				content: '';
				position: absolute;
				@include s('horiz-decor-xs');
				left: calc(50% - 88px); }
			&::before {
				top: -15px; }
			&::after {
				bottom: -15px;
				@include rotate(180); }
			&-decor {
				&::before, &::after {
					content: '';
					position: absolute;
					@include s('horiz-decor-xs_l');
					top: calc(50% - 88px); }
				&::before {
					left: -15px; }
				&::after {
					right: -15px;
					@include rotate(180); } } }
		&-text {
			float: left;
			width: percentage(945 / 1452);
			@include r(1879) {
				float: none;
				width: 100%; } }
		&-descr {
			line-height: 22px; }
		&-sub-title {
			margin: 15px 0;
			@include f('con', 16);
			@include lg {
				margin: 10px 0; }
			@include xs {
				font-size: 17px; } } } }
